<template lang="pug">
    .programmes__item-content
        .programmes__item-container
            .programmes__name {{programme.name}}
            .programmes__info
                img(src='../../../assets/images/icons/dolphin.svg' v-if='index%8===0').programme__img
                img(src='../../../assets/images/icons/dog1.svg' v-if='index%8===1').programme__img 
                img(src='../../../assets/images/icons/chameleon.svg' v-if='index%8===2').programme__img 
                img(src='../../../assets/images/icons/cat.svg' v-if='index%8===3').programme__img 
                img(src='../../../assets/images/icons/cassowary.svg' v-if='index%8===4').programme__img 
                img(src='../../../assets/images/icons/flamingo.svg' v-if='index%8===5').programme__img 
                img(src='../../../assets/images/icons/sloth.svg' v-if='index%8===6').programme__img 
                img(src='../../../assets/images/icons/mouse.svg' v-if='index%8===7').programme__img 
                .programmes__info-container   
                    .programmes__subject Предмет: {{programme.group.subject.name}}
                    .programmes__class Класс: {{programme.group.groupClass}}
                    .programmes__language Язык обучения: {{programme.group.language}}
                    .programmes__status Статус:
                    img(src='../../../assets/images/icons/correct_tick.svg' v-if='status=="ACTIVE"').programme__status-img
                    img(src='../../../assets/images/icons/hourglass.svg' v-if='status=="NOT_ACTIVE"').programme__status-img
                .buttons__programmes-item
                    button(@click.prevent='programmeExpandEvent()' v-if='!programmePointsVisible' :id='programme.id').programmes__view-expand
                    button(@click.prevent='programmeHideEvent()' v-if='programmePointsVisible').programmes__view-hide
            programme-form(
                v-if='programmePointsVisible'
                :programme='programme'
            )
</template>    
<script>
export default {  
    components: {
        programmeForm: () => import("./programme-form"),
        addProgrammeForm: () => import("./add-programme-form")
    },
    data() {
        return {
            programmePointsVisible: false        
        }
    },
    computed: {
        status: function() {
            return this.programme.status
        }
    },

    props: {
        programme: Object,
        index: Number
    },
    methods: {
        programmeExpandEvent(){
            this.programmePointsVisible=!this.programmePointsVisible
            let buttons = document.getElementsByClassName('programmes__view-expand')
            buttons.forEach(elem => {
                elem.disabled=true
            });
            document.getElementById(this.programme.id).removeAttribute('disabled')
        },
        programmeHideEvent() {
            this.programmePointsVisible=!this.programmePointsVisible
            let buttons = document.getElementsByClassName('programmes__view-expand')
            buttons.forEach(elem => {
                elem.disabled=false
            });
        }
    }
}
</script>
