    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
    &:after {
        content: '';
        display: block;
        width: 11px;
        height: 11px;
        background: url(../../../assets/images/icons/expand.svg) center center;
        background-size: cover;
    }
}
.programmes__view-hide {
    background-color:#464549;
    width: 25px;
    height: 27px;
    border-radius: 5px;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
    &:after {
        content: '';
        display: block;
        width: 11px;
        border-radius: 2px;
        height: 3px;
        background: #ffe9ce;
        background-size: cover;
    }
}
.programmes__status {
    margin-right: 7px;
}
